import { WatchlistDataTablePriceCell } from "../../WatchlistDataTableCells/WatchlistDataTablePriceCell"
import { genericColumn } from "../../genericColumn"

function solvencyRatioTtm(tableName: string) {
  const title = "Solvency Ratio (LTM)"
  return genericColumn({
    endpoint: "companyRatios",
    columnKeys: ["solvencyRatioTtm"],
    size: 110,
    title,
    enableSorting: true,
    enableHiding: true,
    tooltip: "Ability to meet long-term obligations.",
    cell: info => {
      return (
        <WatchlistDataTablePriceCell
          tooltip={`Explain the ${title}`}
          queryResult={info.row.original.financialData.companyRatios}
          onClick={() => {
            info.row.original.onChatWithCell?.({
              prompt: `What has ${info.row.original.entityInfo.name} recently reported about solvency ratio?`,
              value: info.row.original.financialData.companyRatios?.data?.solvencyRatioTtm,
              entityInfo: info.row.original.entityInfo,
              columnName: `${tableName}: ${title}`,
            })
          }}
          value={info.row.original.financialData.companyRatios?.data?.solvencyRatioTtm}
        />
      )
    },
  })
}

export { solvencyRatioTtm }
